import React from "react"
import { graphql } from "gatsby"
import { Translation } from "react-i18next"
import i18n from "../lib/i18n"
import Layout from "../components/layout"
import SEO from "../components/seo"
import HeroCareers from "../components/heros/hero-careers"
import Team from "../components/team"
import GetInTouch from "../components/forms/get-in-touch"

export const query = graphql`
  query {
    wpHero(id: { eq: "cG9zdDozMjE=" }) {
      id
      title
      heroFields {
        en {
          headline
          subheadline
          body
          buttontext
          buttonlink
          image {
            id
            sourceUrl
          }
        }
        es {
          headline
          subheadline
          body
          buttontext
          buttonlink
          image {
            id
            sourceUrl
          }
        }
      }
    }
  }
`

const FreeInspection = ({ data }) => {
  const phone = `1-800-123-4567`
  const hero = data.wpHero.heroFields
  return (
    <Layout>
      {i18n.language === "en" ? (
              <SEO title="Free HVAC Inspections | Same Day Service 7-Days a Week" />
            ) : (
              <SEO title="Revisiones de Aires y Calentones Gratis | Servicio El Mismo Día Los 7 Días de La Semana" />
            )}
      <Translation>
        {t => (
          <>
            <HeroCareers
              headline={`${hero[i18n.language].headline}`}
              subheadline={`${hero[i18n.language].subheadline}`}
              body={`${hero[i18n.language].body}`}
              image={`${hero[i18n.language].image.sourceUrl}`}
              button1Text={'469-420-5155'}
              button1Link={`tel:${'469-420-5155'}`}
            />
            <Team />
            <GetInTouch
              locationName="Air Rey A/C &amp; Heating"
              // address="5504 Bandera Rd. Suite 202"
              city="Serving Austin, Houston, Dallas, &amp; San Antonio"
              state=""
              zip=""
              phone="469-420-5155"
              email="sales@airreydfw.com"
            />
          </>
        )}
      </Translation>
    </Layout>
  )
}

export default FreeInspection
